<template lang="en">
<div class="my-popups">
    <div class="modal d-block ">
        <div class="modal-dialog modal-dialog-centered ui-draggable">
            <div class="modal-content">

                <div class="modal-header ui-draggable-handle  py-0">
                    <h4 name="header" class="modal-title mt-2 pt-1">
                        {{ $t('Add') }}
                    </h4>
                    <button class="close" v-on:click="closePopup"><em aria-hidden="true" class="fa fa-times"></em> </button>
                </div>

                <div class="listing py-3 px-3">
                    <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                </div>
                <div class="colorpick">
                    
                    <ColourPicker v-model="color_code" no-input value="color_code" picker="chrome" @input="updateValue" />
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../services/DataService';
import ColourPicker from 'vue-colour-picker'

export default {
    props: {
        AssignCat: {
            type: Array,
            required: true
        },
        CategorytList: {
            type: Array,

        }
    },
    components: {
        ColourPicker
    },
    data() {
        return {
            color_code:'',
            dropdown:[],
            dropdownArray:[],
            colour: '',
            applicationList: [],
            NewArray: [],
            FormSchema: [{
                    layoutType: "single",
                    Data: [{
                            astype: "TextField",
                            label: this.$t('Title'),
                            name: "title",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": true
                            },
                        },

                    ],
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('Selectcolor'),
                        name: "color_code",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: [],

                        },
                    }, ],
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "CustomSelectField",
                        label: this.$t('AppWebsite'),
                        name: "app_ids",
                        value: "",
                        mode: "tag",
                        placeholder: "",
                        config: {
                            options: [],

                        },
                    }, ],
                },
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup,
                },
            ],
            DataLoaded: 0,

        }
    },
    created: async function  () {

        this.DataLoaded = 1;
       await this.FetchData();
        this.EditData(this.AssignCat);
    },
    methods: {
        async FetchData() {
            debugger;
            var vueObj = this;
            vueObj.isLoading = true; {
                await DataService.GetScreenShotListing()
                    .then(function (response) {
                        vueObj.applicationList = response.data;
                        debugger
                       
                        vueObj.applicationList.forEach(item => {
                            let obj1 = {
                                name: item.ApplicationName,
                                value: item.ApplicationId
                            }

                            vueObj.FormSchema[2].Data[0].config.options.push(obj1);
                             vueObj.dropdownArray.push(obj1);
                        });
                        vueObj.NewArray=[];
                        vueObj.CategorytList.forEach((ele => {
                            if(ele.ApplicationsIds!=null){
                            if (ele.ApplicationsIds.length > 1) {
                                var values = ele.ApplicationsIds.split(",");
                                values.forEach(item => {
                                    if(vueObj.NewArray.length>0){
                                        vueObj.applicationList = vueObj.NewArray
                                        vueObj.NewArray=[];
                                    }
                                    vueObj.applicationList.forEach(element => {
                                        if (element.ApplicationId != item) {
                                            vueObj.NewArray.push(element);
                                        }
                                    })
                                });
                            }
                        }
                        }))
                    });
                vueObj.FormSchema[2].Data[0].config.options=[];
                vueObj.NewArray.forEach(item => {
                    let obj1 = {
                        name: item.ApplicationName,
                        value: item.ApplicationId
                    }
                    vueObj.FormSchema[2].Data[0].config.options.push(obj1);
                       
                    
                });
            }
        },

         EditData(ths) {
            var vm = this;
            var obj1 = {};
            var TagsSelectedArray = [];
            var categoriesId
            if(ths.category_id==undefined){
               categoriesId = 0;
            }
            else{
                categoriesId=ths.category_id
            }
            var url = `categoryId=${categoriesId}`;
             DataService.GetCategoryById(url).then(response => {
                var Categories=JSON.parse(response.data);
                debugger;
                vm.color_code='';
                vm.color_code=Categories.data[0].color_code;
                vm.FormSchema[0].Data[0].value = Categories.data[0].category_name;
                 vm.FormSchema[1].Data[0].value=Categories.data[0].color_code   ;
                var appdata = ths.Applications.split(",")
                 vm.dropdownArray
                  var combinedString =appdata.map(str => `"${str.trim()}"`).join(', ');
                  var Appdatas=combinedString.slice(1, -1);
            // =    vm.dropdownArray.map(item => appdata)
            var datadropdown=[]

          datadropdown =  vm.dropdownArray.filter(function(item,index){
          
          if(Appdatas.includes(item.name)) {
          vm.dropdown.push(item)
          }

           });

                vm.dropdown.forEach(item => {
                    TagsSelectedArray.push(obj1 = {
                        name: item.name,
                        value:item.value,
                        label: item.name
                    })
                });

                vm.FormSchema[2].Data[0].value = TagsSelectedArray;

            })
        },

        async onSubmit(formfields, e) {
            var vm = this;
            var ids = '';
            formfields.app_ids.forEach(item => {
                ids += item.value + ','
            });
            if (vm.AssignCat != null || vm.AssignCat != undefined) {
                var catId = vm.AssignCat.category_id;
            } else {
                catId = 0;
            }
            var obj = {
                category_id: catId,
                title: formfields.title,
                color_code: formfields.color_code,
                app_ids: ids.length > 0 ? ids.substring(0, ids.length - 1) : '',
            }
            vm.isLoading = true;
            var formData = new FormData();
            formData.append("postString", JSON.stringify(obj));
            await DataService.ManageCategory(obj).then(response => {
                vm.isLoading = false;
                if (response.data.status == 1) {
                    vm.ShowAlert(vm.$t('Categoryiscreated'), "success", false, vm.$t("Alert"));
                } else if (response.data.status == 2) {
                    vm.ShowAlert(vm.$t('CategoryupdatedSuccessfully'), "success", true, vm.$t('Alert'));
                } else if (response.data.status == 3) {
                    vm.ShowAlert(vm.$t('CategoryAlreadyExists'), "success", true, vm.$t('Alert'));
                }
                vm.$parent.FetchData();
                vm.closePopup();
            })
        },
        
        updateValue(value) {
            var vm = this;
            debugger;
               
            vm.FormSchema[1].Data[0].value = value;
        },
        closePopup() {
            var vm = this;         
            vm.$parent.CloseCategoryClick();
        },

    }

}
</script>

<style scoped>
.colorpick {
    position: absolute;
    right: 19px;
    top: 46%;
    width: auto !important;
    height: 28px;
    z-index: 1;
}

.current-color {
    height: 30px;
}
</style>
